.col-container{
    display: flex;
    width: 100%;
}

.col{
    flex: 1 1;
    /* min-height: 600px; */
}

.text{
    padding: 30px;
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
}

h3{
    color: #C29F79;
    margin-top: 0px !important;
    padding-bottom: 15px !important;
    border-style: solid;
    border-width: 0 0 3px 0;
    border-image: linear-gradient(
        to left,
        rgba(255,255,255,1) 50%,
        rgba(194,159,121, 1) 100%
    ) 0 0 100% 0/0 0 3px 0 stretch;
}

.presentation-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.parallax-img-right{
    max-width: 100%;
    height: 100%;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: 42%;
    background-position-x: right;
    background-position-y: center;
}

.parallax-img-left{
    max-width: 100%;
    height: 100%;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: center;
}

@media only screen and (max-device-width: 1366px) {
    .block-text-img {
        display: flex;
        flex-direction: column-reverse;
    }

    .parallax-img-right {
      background-size: 150%;
      height: 50vh;
    }
    .parallax-img-left{
        background-size: 300%;
        height: 50vh;
      }
  }

@media (max-width: 600px) {
    .col-container {
        display: flex;
        width: 100%;
    }

    .block-img-text{
        display: flex;
        flex-direction: column;
    }

    .block-text-img {
        display: flex;
        flex-direction: column-reverse;
    }

    .parallax-img-left{
        background-size: 325%; 
    }
    .parallax-img-right{
        background-size: 180%;
    }
}


.horizontal-frame {
    height: 100%;
    width: 100%;
    white-space: nowrap;
    margin: 1em 0;
}

.horizontal-card {
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 100%;
    vertical-align: middle;
    margin: auto;
    display: flex;
    border-radius: 10px;
}

.horizontal-card:hover {
    border-radius: 10px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    cursor: pointer;
}

.horizontal-painting {
    display: flex;
    justify-content: center;
    height: 25rem;
    vertical-align: middle;
    width: 40%;
}

.horizontal-painting > img{
    object-fit: cover;
    max-width: 100%;
}
.horizontal-legend{
    padding-left: 3rem;
    border-left: 5px solid #f2f2f2;
    display: block;
}

p{
    white-space: pre-line;
}

@media (max-width: 600px) {

    .horizontal-painting{
        height: 25vh;
    }

    .horizontal-legend{
        padding: 0 10px;
    }

    .horizontal-legend > h5{
        margin: 10px auto;
    }
}
