.modal {
    display: block; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 6; /* Sit on top */
    padding-top: 10px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
  }
  
  /* Modal Content (image) */
  .modal-content {
    margin: auto 240px auto auto;
    display: block;
    width: 80%;
    max-width: 700px;
    height: 100vh;
    object-fit: contain;
  }
  
  /* Caption of Modal Image */
  .caption {
    margin: auto 240px auto auto;
    display: block;
    width: 80%;
    max-width: 700px;
    text-align: center;
    color: #ccc;
    padding: 10px 0;
    height: 150px;
  }

  .caption .caption-title{
    font-size: 30px;
    font-weight: bold;
    line-height: 100%;
  }
  
  .caption .caption-detail{
    padding-top: 10px;
    margin-top: 20px;
    line-height: 100%;
    font-size: 25px;
    font-weight: normal;
  }

  /* Add Animation */
  .modal-content, .caption {  
    -webkit-animation-name: zoom;
    -webkit-animation-duration: 0.6s;
    animation-name: zoom;
    animation-duration: 0.6s;
  }
  
  @-webkit-keyframes zoom {
    from {-webkit-transform:scale(0)} 
    to {-webkit-transform:scale(1)}
  }
  
  @keyframes zoom {
    from {-webkit-transform:scale(0);transform:scale(0)} 
    to {-webkit-transform:scale(1);transform:scale(1)}
  }
  
  /* The Close Button */
  .close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
  }
  
  .close:hover,
  .close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }
  
  .modal-left {
    position: absolute;
    top: 50%;
    left: 275px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
  }

  .modal-left:hover,
  .modal-left:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }

  .modal-right {
    position: absolute;
    top: 50%;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
  }
  .modal-right:hover,
  .modal-right:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }

  /* 100% Image Width on Smaller Screens */
  @media only screen and (max-width: 600px){
    .modal-content {
      width: 100%;
    }

    .modal-left {
        left: 25px;
    }

    .modal-right {
        right: 25px;
    }

    .caption {
        margin: -150px auto auto auto;
      }
}
