.uploader {
   margin-left: auto;
   margin-right: auto;
   margin-top: 1.5rem;
   background-color: rgba(0, 0, 0, 0.02);
   cursor: pointer;
   display: flex;
   height: 300px;
   justify-content: center;
   outline: 1px dashed #ccc;
   outline-offset: 5px;
   position: relative;
   width: 300px;
}
   
.uploader input {
    display: none; 
}
   
.uploader img,
.uploader .icon { 
    pointer-events: none; 
}
   
.uploader, 
.uploader .icon {
    transition: all 100ms ease-in;
}
   
.uploader .icon {
    color: #eee;
    color: rgba(0, 0, 0, 0.2);
    font-size: 5em;
}
   
.uploader img {
    left: 50%;
    opacity: 0;
    max-height: 100%;
    max-width: 100%;
    position: absolute;
    top: 50%;
    transition: all 300ms ease-in;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: auto;
}
.uploader .loaded {
    opacity: 1;
}

